body, html, #root, .app-container {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Inter', Arial, sans-serif;
  background: #181a1b;
}
.app-container {
  min-height: 100vh;
  background: #181a1b;
  color: #f7f7f9;
  transition: background 0.2s, color 0.2s;
}

.comic-header {
  width: 100%;
  text-align: center;
  font-family: 'Comic Sans MS', 'Comic Sans', cursive, sans-serif;
  font-size: 2.2rem;
  letter-spacing: 2px;
  color: #ffeb3b;
  text-shadow: 2px 2px 0 #222, 4px 4px 0 #007aff;
  margin: 0 0 18px 0;
  padding: 18px 0 0 0;
  user-select: none;
}

.main-area {
  max-width: 420px;
  margin: 0 auto;
  padding: 16px 8px 16px 8px;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  box-sizing: border-box;
}
.avatar-area {
  display: flex;
  justify-content: center;
  margin-bottom: 16px;
}
.chat-area {
  flex: 1;
  overflow-y: auto;
  margin-bottom: 16px;
  padding-bottom: 8px;
  max-height: 60vh;
  scroll-behavior: smooth;
}
@media (max-width: 600px) {
  .main-area {
    padding: 8px 2px 8px 2px;
  }
  .chat-area {
    max-height: 50vh;
  }
  .comic-header {
    font-size: 1.4rem;
    padding: 10px 0 0 0;
  }
} 